import React from "react";
import "./Card.css";
import PropTypes from "prop-types";

export const Card = ({ lang, url, fcolor, scolor }) => {
  return (
    <div
      className="card"
      style={{
        background: `linear-gradient(to left, ${fcolor}, ${scolor})`,
        borderRadius: "50px",
        textShadow: "22px black",
      }}
    >
      <img src={url} alt="lang.svg"></img>
      <h3>{lang}</h3>
    </div>
  );
};

Card.propTypes = {
  lang: PropTypes.string,
  url: PropTypes.string,
};

export default Card;
